import { useEffect, useState } from "react"
import ButtonBack from "../Components/Buttons/ButtonBack"
import PageContent from "../Components/PageContent"
import PageTitle from "../Components/PageTitle"
import { Link, useParams } from "react-router-dom"
import FormGroup from "../../Components/FormGroup"
import { get } from "../../Functions/Api"
import Card from "../../Components/Card"
import FormTextarea from "../../Components/FormTextarea"

const DetailAnggota = () => {
    const [detailAnggotaKK, setDetailAnggotaKK] = useState<DataT>()
    const { no_kk, nik, type } = useParams()
    const ttl = (tempat: string, tanggal: string) => {
        return tempat || tanggal ? `${tempat}, ${tanggal}` : "-"
    }

    const jenisKelamin = {
        L: "Laki-laki",
        P: "Perempuan"
    }

    useEffect(() => {
        const getDetailAnggotaKK = async () => {
            const res = await get(`anggota-keluarga/${nik}?no_kk=${no_kk}`)
            if (res.ok) setDetailAnggotaKK(res.data)
        }
        getDetailAnggotaKK()
    }, [nik, no_kk])

    return (
        <>
            <PageTitle title="Data Keluarga" />
            <ButtonBack />
            <PageContent>
                <Card
                    cardHeader={
                        <h4 className="card-title text-success">
                            Detail Anggota
                        </h4>
                    }
                    cardBody={
                        <form>
                            <div className="row">
                                {detailAnggotaKK && (
                                    <>
                                        <div className="col-md-6">
                                            <FormGroup
                                                label="Nama"
                                                type="text"
                                                id="nama"
                                                name="nama"
                                                defaultValue={
                                                    detailAnggotaKK.nama
                                                }
                                                disabled
                                            />
                                            <FormGroup
                                                label="NIK"
                                                type="number"
                                                id="nik"
                                                name="nik"
                                                defaultValue={
                                                    detailAnggotaKK.nik
                                                }
                                                disabled
                                            />
                                            <FormGroup
                                                label="Tempat"
                                                type="text"
                                                id="tempat"
                                                name="ttl"
                                                defaultValue={ttl(
                                                    detailAnggotaKK.tempat_lahir,
                                                    detailAnggotaKK.tanggal_lahir
                                                )}
                                                disabled
                                            />
                                            <FormGroup
                                                label="Status"
                                                type="text"
                                                id="status"
                                                name="status"
                                                defaultValue={
                                                    detailAnggotaKK.status ||
                                                    "-"
                                                }
                                                disabled
                                            />
                                            <FormGroup
                                                label="Status Perkawinan"
                                                type="text"
                                                id="status-perkawinan"
                                                name="status-perkawinan"
                                                defaultValue={
                                                    detailAnggotaKK.status_perkawinan ||
                                                    "-"
                                                }
                                                disabled
                                            />
                                            <FormGroup
                                                label="Jenis Kelamin"
                                                type="text"
                                                id="jenis-kelamin"
                                                name="jenis-kelamin"
                                                defaultValue={
                                                    jenisKelamin[
                                                        detailAnggotaKK.jenis_kelamin as JenisKelaminT
                                                    ] || "-"
                                                }
                                                disabled
                                            />
                                        </div>
                                        <div className="col-md-6">
                                            <FormGroup
                                                label="Nomor Whatsapp"
                                                type="number"
                                                id="no-wa"
                                                name="no_whatsapp"
                                                defaultValue={
                                                    detailAnggotaKK.penduduk
                                                        .no_whatsapp || "0"
                                                }
                                                disabled
                                            />
                                            <FormGroup
                                                label="Pendidikan"
                                                type="text"
                                                id="pendidikan"
                                                name="pendidikan"
                                                defaultValue={
                                                    detailAnggotaKK.pendidikan ||
                                                    "-"
                                                }
                                                disabled
                                            />
                                            <FormGroup
                                                label="Agama"
                                                type="text"
                                                id="agama"
                                                name="agama"
                                                defaultValue={
                                                    detailAnggotaKK.agama || "-"
                                                }
                                                disabled
                                            />
                                            <FormTextarea
                                                id="alamat"
                                                label="Alamat"
                                                name="alamat"
                                                defaultValue={
                                                    detailAnggotaKK.alamat
                                                        .alamat || "-"
                                                }
                                                rows={5}
                                                disabled
                                            />
                                        </div>
                                    </>
                                )}
                            </div>
                            <div className="col-md-12 d-flex justify-content-end gap-3 mb-5">
                                <Link
                                    to={`/${type}/data-keluarga/${no_kk}/${nik}/edit`}
                                    className="btn btn-primary"
                                >
                                    Perbarui Anggota Keluarga
                                </Link>
                                <button className="btn btn-danger">
                                    Hapus
                                </button>
                            </div>
                        </form>
                    }
                />
            </PageContent>
        </>
    )
}

export default DetailAnggota
